import React from 'react'

import {connect} from 'react-redux'
import store from '../store'

import './Home.css'

const logo = require('./Images/logo.png')

function Home(props) {
    store.dispatch({type: 'SET-PAGES', page: 'home'});
    return(
        <div className='mainblock'>
            <div className='section'>
                <div className='sectionHeader'>
                    <h1>О чем сайтик</h1>
                </div>
                <div className='subSection'>
                    <div className='leftSubSection'>
                        <img src={logo} alt='logo' style={{width: '60%', borderRadius: '50%', marginLeft: '20%'}} />
                    </div>
                    <div className='rightSubSection'>
                        <p>Сайтик о моих интересах, проектах.</p>
                        <p>Всё что здесь будет публиковаться, это полет моей фантазии, что-то мне надо делать по работе, что-то по учебе, а что-то для себя.</p>
                        <p>Скорее всего здесь будет еще что-то типа блога.</p>
                    </div>
                </div>
            </div>

            <div className='section'>
                <div className='sectionHeader'>
                    <h1>Немного об авторе</h1>
                </div>
                <div className='subSection'>
                    <div className='leftSubSection'>
                        <img src={require('./Images/myphoto.jpeg')}
                            alt='avatar'
                            style={{width: '50%', borderRadius: '50%', marginLeft: '25%'}}
                            />
                    </div>
                    <div className='rightSubSection'>
                        <p>Зовут меня Миша Ершов.</p>
                        <p>Мне нравится создавать полноценные приложения (веб, мобильные). Когда пишу проекты, весь код - это полёт фантазии. Прлучаются довольно необычные, на первый взляд странные,
                            непонятные решения, но они работают.
                        </p>
                        <p>В основном пишу проекты один и фронт и бэк.</p>
                        <p>Cейчас я учусь в ИТМО, пока что не работаю.</p>
                    </div>
                </div>
            </div>

            <div className='section'>
                <div className='sectionHeader'>
                    <h1>Немного о моих интересах и о том, чем я занимаюсь</h1>
                </div>
                <div className='subSection'>
                    <div className='leftSubSection'>
                        <img 
                            src={require('./Images/webDev.png')}
                            width='90%'
                            style={{paddingLeft: '5%'}}
                            alt='logos'
                        />
                    </div>
                    <div className='rightSubSection'>
                        <h2>Веб приложения</h2>
                        <p>Программирую я с детства, но веб приложения начал писать относительно недавно. Началось всё с изучения Ruby, было довольно легко т.к. этот язык очень похож на
                            python, но как-то не зашло. Потом я узнал о ReactJS, собственно сейчас я использую его, довольно хороший инструмент.</p>
                        <p>Бэкенд пишу в основном на NodeJS. Многие говорят, что этот язык не предназначен для этого, но, как по мне, у JS довольно большое будущее + мне нравится, что и клинтская часть
                            и серверная чать пишутся на одном языке.</p>
                        <h2>Мобильные приложения</h2>
                        <p>И тут опять же JS. Разрабатываю мобильные кроссплатформенные приложения на React Native. По мере создания приложений буду писать о них а разделе "Проектики"</p>
                        <h2>3D модельки и печать</h2>
                        <p>Еще одно моё увлечение - это 3D моделирование и печать, довольно увлекательно что-то придумывать, а потом получать материальную копию твоих мыслей. Сейчас, конечно,
                            больше времени уделяю разработке веб приложений (и фротенд и бэкенд), но надеюсь, что скоро и для моделирования найдется время.</p>
                    </div>
                </div>
            </div>

            <div className='section'>
                <div className='sectionHeader'>
                    <h1>Что умею</h1>
                </div>
                <div className='subSection'>
                    <div className='leftSubSection'>
                        <img 
                            src={require('./Images/logos.png')}
                            width='90%'
                            style={{paddingLeft: '5%'}}
                            alt='logos'
                        />
                    </div>
                    <div className='rightSubSection'>
                        <ul>
                            <li>React JS</li>
                            <li>React Native</li>
                            <li>Node JS</li>
                            <li>MongoDB</li>
                            <li>Python</li>
                            <li>C++</li>
                            <li>C</li>
                            <li>C#</li>
                            <li>MySQL</li>
                            <li>MsSQL</li>
                            <li>Django</li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    )
}

const mapStateToProps = (store) => {
    return {page: store.pageState};
}

export default connect(mapStateToProps)(Home);